
import { defineComponent } from 'vue';
import type {ConfigurationInquiry,COM21000111_RES, COM21000111_REQ_BODY, MessageDescription} from "@/shared/type/COM21000111.type";
import type {COM21000131_REQ_BODY,COM21000131_RES} from "@/shared/type/COM21000131.type";
import {COMMON_KEY_VALUE} from "@/shared/constants/common.const";
import { modalController } from '@ionic/vue';
import { APM20200113_RES } from "@/shared/type/APM20200113.type";
import { screenMode } from "@/shared/type/Approval.type";

export default defineComponent({
    name: "com210001ss01",
    data(){
        return{
            COMMON_KEY_VALUE,
            isDisable: true,
            inRequiredInput: false,
            approvalRes: {} as any,
			operationMode: 'normal' as screenMode,

            //config
            isReady: false,
            userConfig: {} as ConfigurationInquiry,
            pwConfig: {} as ConfigurationInquiry,
            format: {
                userId: 'both' as 'both' | 'character' | 'number',
                pwd: 'both' as 'both' | 'character' | 'number'
            },
            //message
            disabledSave: false,
            currentTab: 'En',
            errorMessages: [] as MessageDescription[]
        }
    },

    watch: {
        // clear character and number length if choose N
        userConfig : {
            handler(config: ConfigurationInquiry){
                if(config.characterYN == 'N') {
                    this.userConfig.userIDCharacterLength = 0
                } else if(config.numberYN == 'N') {
                    this.userConfig.userIDNumberLength = 0
                }
            },
            deep: true
        },

        pwConfig : {
            handler(config: ConfigurationInquiry){
                if(config.characterYN == 'N') {
                    this.pwConfig.userIDCharacterLength = 0
                } else if(config.numberYN == 'N') {
                    this.pwConfig.userIDNumberLength = 0
                }
            },
            deep: true
        },

        'format.userId' : function(format) {
            switch (format) {
                case 'both':
                    this.userConfig.characterYN = 'Y'
                    this.userConfig.numberYN = 'Y'
                    break;
                case 'character':
                    this.userConfig.characterYN = 'Y'
                    this.userConfig.numberYN = 'N'
                    break;
                case 'number':
                    this.userConfig.characterYN = 'N'
                    this.userConfig.numberYN = 'Y'
                    break;
            }
        },

        'format.pwd' : function(format) {
            switch (format) {
                case 'both':
                    this.pwConfig.characterYN = 'Y'
                    this.pwConfig.numberYN = 'Y'
                    break;
                case 'character':
                    this.pwConfig.characterYN = 'Y'
                    this.pwConfig.numberYN = 'N'
                    break;
                case 'number':
                    this.pwConfig.characterYN = 'N'
                    this.pwConfig.numberYN = 'Y'
                    break;
            }
        }
    },

    computed: {
        approvalType(): string {
            return this.$route.query.approvalType ? this.$route.query.approvalType.toString() : '';
        },

        invalidUserIdLength(): boolean {
            return Number(this.userConfig.maxLength) < Number(this.userConfig.userIDCharacterLength) + Number(this.userConfig.userIDNumberLength)
        },
    },

    async mounted(){
        if(this.approvalType==''){
            this.getInternetBankingConfigurationData();
            // this.$emit('operationMode', this.operationMode);
            // this.$emit('approvalResponse', this.approvalRes);
        }else{
            await this.$approval.getAuthorizationDetail().then((authDetail: APM20200113_RES) => {
				let authDetailParsed = JSON.parse(authDetail.body.requestContent);
				this.approvalRes = authDetail.body;
				this.operationMode = this.approvalType == 'my-request' ? 'read' : this.approvalType == 'my-approval' ? 'approval' : 'normal';

                this.$emit('operationMode', this.operationMode);
                this.$emit('approvalResponse', this.approvalRes);

                authDetailParsed.body.idPwConfig.forEach((config: ConfigurationInquiry) => {
                    switch (config.bizCode) {
                        case '001':
                            this.userConfig = config;
                            break;
                        case '002':
                            this.pwConfig = config;
                            break;
                    }
                });
            })
        }
    },

    methods:{
        handleEvent(event: string){
            switch(event){
                case 'cancel':
                    this.$dialog.onOpen({
                        dialogMode:"Question",
                        content: `${this.$t("message.are_you_sure_you_want_to_cancel_without_saving_data")}`,
                        callback: () => {
                            this.isDisable = true;
                            this.$emit('isResetEditable', false);
                            this.$emit('isTapChange', true);
                            modalController.dismiss();
                        }
                    });
                    break;
        
                case 'edit':
                    this.isDisable = false;
                    break;

                case 'save':
                    if( this.checkValidateUserId() && this.checkValidatePwd() && !this.disabledSave ){
                        this.$dialog.onOpen({
                            dialogMode:"Question",
                            content: `${this.$t("message.would_you_like_to_save")}`,
                            callback: async () => {
                                await this.updateInternetBankingConfiguration().then((result: boolean) => {
                                    if(result){
                                        this.isDisable = true;
                                        this.$emit('isResetEditable',false);
                                        this.$emit('isTapChange',true);
                                    }
                                });
                                modalController.dismiss();
                            }
                        });
                    } else {
                        this.inRequiredInput = true;
                    }
                    break;
            }
        },

        async getInternetBankingConfigurationData() {
            const reqBody : COM21000111_REQ_BODY = {
                bizCode: "001,002",
            }
            
            this.isReady = false
            const responseData = await this.$request.getRequest('COM21000111', reqBody, false) as COM21000111_RES

            if(responseData.header.result){
                responseData.body.list.forEach((config: ConfigurationInquiry) => {
                    switch (config.bizCode) {
                        case '001':
                            this.userConfig = config;
                            this.format.userId = this.handleFormat(config)
                            break;
                        case '002':
                            this.pwConfig = config;
                            this.format.pwd = this.handleFormat(config)
                            this.errorMessages = config.messageDescList
                            break;
                    }
                });
                this.isReady = true
            }
        },

        async updateInternetBankingConfiguration() : Promise<boolean> {
            const reqBody : COM21000131_REQ_BODY = {
                list: [this.userConfig, this.pwConfig]
            };
          
            const responseData = await this.$request.getRequest('COM21000131', reqBody, false, 'COM210001SS01') as COM21000131_RES;
            
            if(this.approvalType){
                this.isDisable = true;
            }

            if (responseData.header.result) {
                this.getInternetBankingConfigurationData();
                this.isDisable = true;
                this.$dialog.onOpen({
                    content: `${this.$t("message.update_success")}`,
                    dialogMode:"Success",
                    callback: () => {
                        modalController.dismiss();
                    }
                });
            }

            return responseData.header.result
        },

        handleValidateLength(config: 'UserID' | 'PW') : boolean {
            if(config == 'UserID') {
                const sum = Number(this.userConfig.userIDCharacterLength) + Number(this.userConfig.userIDNumberLength)
                if(this.userConfig.characterYN == 'Y' && this.userConfig.numberYN == 'Y'){
                    return sum >= Number(this.userConfig.minLength) && sum <= Number(this.userConfig.maxLength)
                } else if(this.userConfig.characterYN == 'Y' && this.userConfig.numberYN == 'N') {
                    return Number(this.userConfig.userIDCharacterLength) >= Number(this.userConfig.minLength) && Number(this.userConfig.userIDCharacterLength) <= Number(this.userConfig.maxLength)
                } else if(this.userConfig.characterYN == 'N' && this.userConfig.numberYN == 'Y') {
                    return Number(this.userConfig.userIDNumberLength) >= Number(this.userConfig.minLength) && Number(this.userConfig.userIDNumberLength) <= Number(this.userConfig.maxLength)
                }
            } else if(config == 'PW') {
                const sum = Number(this.pwConfig.userIDCharacterLength) + Number(this.pwConfig.userIDNumberLength)
                return sum >= Number(this.pwConfig.minLength) && sum <= Number(this.pwConfig.maxLength)
            }
            return false
        },

        checkValidateUserId() : boolean {
            return  this.userConfig.minLength >= 6 && this.userConfig.minLength <= 20 &&
                    this.userConfig.maxLength >= 6 && this.userConfig.maxLength <= 20 &&
                    Number(this.userConfig.minLength) <= Number(this.userConfig.maxLength) && 
                    !this.invalidUserIdLength && this.handleValidateLength('UserID')
        },

        checkValidatePwd() : boolean {
            return  this.pwConfig.minLength <= this.pwConfig.maxLength && 
                    this.pwConfig.maxLength >= 5 && 
                    this.pwConfig.maxLength <= 256
        },

        checkValidateFormat(){
            return  (this.format.userId == 'both' && Number(this.userConfig.userIDNumberLength) <= 0 || 
                    this.format.userId == 'number' && Number(this.userConfig.userIDNumberLength) <= 0) ||
                    (this.format.userId == 'both' && Number(this.userConfig.userIDCharacterLength) <= 0 || 
                    this.format.userId == 'character' && Number(this.userConfig.userIDCharacterLength) <= 0)
        },

        handleFormat(config: ConfigurationInquiry) : 'both' | 'character' | 'number' {
            if(config.characterYN === 'Y' && config.numberYN === 'Y') {
                return 'both'
            } else if(config.characterYN === 'Y' && config.numberYN === 'N') {
                return 'character'
            } else {
                return 'number'
            }
        }
    }
})
